/*wooProductRatings*/
addFilter('PostGridElementOptionswooProductRatings', 'wcps/element', PostGridElementOptionswooProductRatings, 10)

function PostGridElementOptionswooProductRatings(html, selectedElement, handleChange) {

  var summaryTypeArgs = {
    pgNone: { label: __("None", "woocommerce-products-slider"), value: "" },
    pg01: {
      label: __("123 customer reviews", "woocommerce-products-slider"),
      value: "{review_count} customer reviews",
    },
    pg02: {
      label: __("4.50/5.00", "woocommerce-products-slider"),
      value: "{average_rating}/5.00",
      isPro: true,
    },
    pg03: {
      label: __("4.50 out of 5.00", "woocommerce-products-slider"),
      value: "{average_rating} out of 5.00",
      isPro: true,
    },
    pg04: {
      label: __("4.50(123 reviews)", "woocommerce-products-slider"),
      value: "{average_rating}({review_count} reviews)",
      isPro: true,
    },
    pg05: {
      label: __("Custom", "woocommerce-products-slider"),
      value: "custom",
      isPro: true,
    },
  };

  return (
    <div>

      <PanelRow>
        <label htmlFor="" className="font-medium text-slate-900 ">
          {__("Class", "woocommerce-products-slider")}
        </label>
        <InputControl
          value={selectedElement?.options?.class}
          onChange={(newVal) => {
            handleChange("class", newVal);
          }}
        />
      </PanelRow>
      <PanelRow>
        <label htmlFor="" className="font-medium text-slate-900 ">
          {__("Default Rating", "woocommerce-products-slider")}
        </label>
        <InputControl
          value={selectedElement?.options?.defaultRating}
          onChange={(newVal) => {
            handleChange("defaultRating", newVal);
          }}
        />
      </PanelRow>

      <PanelRow>
        <label htmlFor="" className="font-medium text-slate-900 ">
          {__("Idle Icon", "woocommerce-products-slider")}
        </label>
        <PGIconPicker
          library={selectedElement.options.iconsIdle.library}
          srcType={selectedElement.options.iconsIdle.srcType}
          iconSrc={selectedElement.options.iconsIdle.iconSrc}
          onChange={(arg) => {
            handleChange("iconsIdle", {
              srcType: arg.srcType,
              library: arg.library,
              iconSrc: arg.iconSrc,
            });
          }}
        />
      </PanelRow>
      <PanelRow>
        <label htmlFor="" className="font-medium text-slate-900 ">
          {__("Idle Icon", "woocommerce-products-slider")}
        </label>
        <PGIconPicker
          library={selectedElement.options.iconsFilled.library}
          srcType={selectedElement.options.iconsFilled.srcType}
          iconSrc={selectedElement.options.iconsFilled.iconSrc}
          onChange={(arg) => {
            handleChange("iconsFilled", {
              srcType: arg.srcType,
              library: arg.library,
              iconSrc: arg.iconSrc,
            });
          }}
        />
      </PanelRow>



      <PGRadioControl
        selected={selectedElement.summaryType}
        options={summaryTypeArgs}
        label="Summary Type"
        name={"summaryType"}
        onChange={(option) => {

          console.log(option);


          handleChange("summaryType", option.value);
        }} />

      {selectedElement.summaryType == "custom" && (
        <>
          <div className="my-3 relative">
            <label
              htmlFor=""
              className="font-medium text-slate-900 ">
              {__("Custom Summary", "woocommerce-products-slider")}
            </label>
            <InputControl
              value={selectedElement.summaryTypeCustom}
              placeholder="{average_rating} out of 5.00"
              onClick={() => {
                if (isProFeature) {
                  setIsVisible(!isVisible);
                }
              }}
              disabled={isProFeature ? true : false}
              onChange={(newVal) => {
                handleChange("summaryTypeCustom", newVal);
              }}
            />
            {isProFeature && isVisible && (
              <a
                href="https://comboblocks.com/pricing/"
                target="_blank"
                className="absolute bottom-2 right-0 bg-gray-700 text-white hover:text-white no-underline px-2 rounded-sm py-1 ">
                {__("Subscribe to use", "woocommerce-products-slider")}
              </a>
              // </div>
            )}
          </div>
          <p>{__("Please use following tags:", "woocommerce-products-slider")}</p>
          <ul>
            <li>
              <code>{"{rating_count}"}</code>
            </li>
            <li>
              <code>{"{review_count}"}</code>
            </li>
            <li>
              <code>{"{average_rating}"}</code>
            </li>
            <li>
              <code>{"{product_title}"}</code>
            </li>
          </ul>
        </>
      )}

      <PanelRow>
        <label htmlFor="" className="font-medium text-slate-900 ">
          {__("Prefix Text", "woocommerce-products-slider")}
        </label>
        <InputControl
          value={selectedElement?.options?.prefixText}
          onChange={(newVal) => {
            handleChange("prefixText", newVal);
          }}
        />
      </PanelRow>
      <PanelRow>
        <label htmlFor="" className="font-medium text-slate-900 ">
          {__("Postfix Text", "woocommerce-products-slider")}
        </label>
        <InputControl
          value={selectedElement?.options?.postfixText}
          onChange={(newVal) => {
            handleChange("postfixText", newVal);
          }}
        />
      </PanelRow>



    </div>
  );
}



